<template>
  <wra-card
    :class="{
      'px-1 py-1': $vuetify.breakpoint.smAndDown,
      'px-6 py-5': $vuetify.breakpoint.mdAndUp
    }"
    color="#F1F1F1"
    outlined
  >
    <transition name="translate" mode="out-in">
      <wra-card-text
        v-if="validationMessage != 'Blank'"
        class="pa-4"
        :key="text.aboutRefund.title"
      >
        <wra-validation-tooltip
          type="wra-error"
          role="status"
          aria-live="polite"
        >
          {{ validationMessage }}
        </wra-validation-tooltip>
      </wra-card-text>
    </transition>

    <transition name="translate" mode="out-in">
      <wra-card-text class="pa-4" :key="text.aboutRefund.title">
        <wra-card-title class="pt-0 px-0 pb-4">
          <wra-header size="h2">{{ text.aboutRefund.title }}</wra-header>
        </wra-card-title>
        <wra-card-text class="pt-0 px-0 pb-2">
          <p v-html="text.aboutRefund.question2"></p>
        </wra-card-text>
        <wra-text-field
          v-model="localInputs.RefundName"
          id="reasonField"
        ></wra-text-field>
        
      </wra-card-text>
    </transition>
    <transition name="translate" mode="out-in">
      <wra-card-actions class="py-2 px-4">
          <v-row>
            <v-col cols="12" md="2" order="first" order-md="last">
              <wra-button
                dark
                class="mb-2"
                @click="nextStage()"
                id="nextButton"
              >
                {{ text.next }}
                <wra-icon alt="" right large>navigate_next</wra-icon>
              </wra-button>
            </v-col>
            <v-col cols="12" md="2" order="last" order-md="first">
              <wra-button
                dark
                class="mr-4 mb-2"
                @click="previousStage"
                id="backButon"
              >
                <wra-icon alt="" left large>navigate_before</wra-icon>
                <span v-html="text.previous"></span>
              </wra-button>
            </v-col>
          </v-row>
        </wra-card-actions>
    </transition>
  </wra-card>
</template>

<script>
export default {
  props: ["text", "inputs"],

  data() {
    return {
    localInputs: {},
    validationMessage: "Blank",
    }
  },
  methods: {
    previousStage: function() {
      // if (this.$root.$data.cookieAccept.websiteUse == "true") {
      //   this.$ga.event(
      //     "LTT Calculator",
      //     "What are the lease start and end dates?",
      //     "Back"
      //   );
      // }
      this.$emit("previous", this.localInputs);
    },
    nextStage: function() {
      if(this.validatePage() == false) {
        this.$emit("scrollUp");
        return
      } else {
      this.validationMessage = "Blank";
      // if (this.$root.$data.cookieAccept.websiteUse == "true") {
      //           this.$ga.event(
      //             "LTT Calculator",
      //             "What is the purchase price?",
      //             this.localInputs.purchasePrice
      //           );
      //         }

        this.$emit("next", this.localInputs);
      }
    },
    validatePage: function() {
      if (
        this.localInputs.RefundName == null ||
        this.localInputs.RefundName == ""
      ) {
        this.validationMessage = this.text.validation.emptyField;
        return false;
      } else {
        return true;
      }
    }
  },
  created() {
    this.localInputs = JSON.parse(JSON.stringify(this.inputs));
  },
  mounted() {
    this.$emit("scrollUp");
  }
};
</script>
