<template>
  <wra-card
    :class="{
      'px-1 py-1': $vuetify.breakpoint.smAndDown,
      'px-6 py-5': $vuetify.breakpoint.mdAndUp
    }"
    color="#F1F1F1"
    outlined
  >
    <transition name="translate" mode="out-in">
      <wra-card-text
        v-if="validationMessage != 'Blank'"
        class="pa-4"
        :key="text.aboutUser.title"
      >
        <wra-validation-tooltip
          type="wra-error"
          role="status"
          aria-live="polite"
        >
          {{ validationMessage }}
        </wra-validation-tooltip>
      </wra-card-text>
    </transition>

    <transition name="translate" mode="out-in">
      <wra-card-text class="pa-4" :key="text.aboutUser.title">
        <wra-card-title class="pt-0 px-0 pb-4">
          <wra-header size="h2">{{ text.aboutUser.title }}</wra-header>
        </wra-card-title>
        <wra-card-text class="pt-0 px-0 pb-0">
          <p v-html="text.aboutUser.question4"></p>
        </wra-card-text>
        <wra-radio-group v-model="localInputs.declareForm">
          <template v-slot:label>
            {{ text.aboutRefund.question4 }}
          </template>
          <wra-radio :value="text.aboutUser.question4radio[0].value">
            <template v-slot:label>
              <p v-html="text.aboutUser.question4radio[0].label"></p>
            </template>
          </wra-radio>
          <wra-radio :value="text.aboutUser.question4radio[1].value">
            <template v-slot:label>
              <p v-html="text.aboutUser.question4radio[1].label"></p>
            </template>
          </wra-radio>
        </wra-radio-group>
        
      </wra-card-text>
    </transition>
    <transition name="translate" mode="out-in">
      <wra-card-actions class="py-2 px-4">
          <v-row>
            <v-col cols="12" md="2" order="first" order-md="last">
              <wra-button
                dark
                class="mb-2"
                @click="nextStage()"
                id="nextButton"
              >
                {{ text.next }}
                <wra-icon alt="" right large>navigate_next</wra-icon>
              </wra-button>
            </v-col>
            <v-col cols="12" md="2" order="last" order-md="first">
              <wra-button
                dark
                class="mr-4 mb-2"
                @click="previousStage"
                id="backButon"
              >
                <wra-icon alt="" left large>navigate_before</wra-icon>
                <span v-html="text.previous"></span>
              </wra-button>
            </v-col>
          </v-row>
        </wra-card-actions>
    </transition>
      <v-overlay :value="overlay"><v-progress-circular
      indeterminate
      color="primary"
    ></v-progress-circular></v-overlay>

  </wra-card>

</template>

<script>
const axios = require("axios");

export default {
  props: ["text", "inputs"],

  data() {
    return {
      overlay: false,
    localInputs: {},
    validationMessage: "Blank",
    }
  },
  methods: {
    previousStage: function() {
      // if (this.$root.$data.cookieAccept.websiteUse == "true") {
      //   this.$ga.event(
      //     "LTT Calculator",
      //     "What are the lease start and end dates?",
      //     "Back"
      //   );
      // }
      this.$emit("previous", this.localInputs);
    },
    nextStage: function() {
      if(this.validatePage() == false) {
        this.$emit("scrollUp");
        return
      } else {
        this.overlay = true;
      this.validationMessage = "Blank";
      //Code below is converting strings into valid C# formats so it can be converted with System.Json
      this.localInputs.IncorrectPaymentAmount = parseFloat(this.localInputs.IncorrectPaymentAmount.replaceAll(",", ""));
      this.localInputs.RefundAmount = parseFloat(this.localInputs.RefundAmount.replaceAll(",", ""));

      var inputDate = new Date(
          this.localInputs.IncorrectPaymentDate.split("/")[1] +
            "/" +
            this.localInputs.IncorrectPaymentDate.split("/")[0] +
            "/" +
            this.localInputs.IncorrectPaymentDate.split("/")[2]
        );

      this.localInputs.IncorrectPaymentDate = inputDate;

      if (this.localInputs.RefundSameAccount == "True") {
        this.localInputs.RefundSameAccount = true;
      } else {
        this.localInputs.RefundSameAccount = false;
      }

      var delayInMilliseconds = 1000; //1 second

      setTimeout(function() {
        this.overlay = false;
        this.$emit("next", this.localInputs);
      }, delayInMilliseconds);
    // axios.post('https://localhost:7075/api/crm/postcontact', this.localInputs)
    //   .then(response => {
    //     this.overlay = false;
    //     this.$emit("next", this.localInputs);
    //     })
    //   .catch(error => {
    //     console.log(error);
    //   });
      // if (this.$root.$data.cookieAccept.websiteUse == "true") {
      //           this.$ga.event(
      //             "LTT Calculator",
      //             "What is the purchase price?",
      //             this.localInputs.purchasePrice
      //           );
      //         }

        
      }
    },
    validatePage: function() {
      if (
        this.localInputs.declareForm == null ||
        this.localInputs.declareForm == ""
      ) {
        this.validationMessage = this.text.validation.radioEmpty;
        return false;
      } else if(this.localInputs.declareForm == "False") {
        this.validationMessage = this.text.validation.declarationInvalid;
        return false;
      } else {
        return true;
      }
    }
  },
  created() {
    this.localInputs = JSON.parse(JSON.stringify(this.inputs));
  },
  mounted() {
    this.$emit("scrollUp");
  }
};
</script>
