<template>
  <v-container
    class="pt-0 pb-3"
    :class="{
      'px-0': $vuetify.breakpoint.smAndDown
    }"
  >
    <!--Heading-->
    <v-row
      no-gutters
      class="pb-6 hidden-print"
      :class="{
        'px-5': $vuetify.breakpoint.smAndDown
      }"
    >
      <v-col lg="2" cols="0"></v-col>
      <v-col lg="8" cols="12">
        <v-divider
          role="presentation"
          style="height: 6px;
          max-height: 6px;
          background: #e5e5e5;
          border: none;"
        ></v-divider>

        <transition name="translate" mode="out-in">
          <wra-subheader class="mt-8" :key="text.header">
            {{ text.subheader }}
          </wra-subheader>
        </transition>
        <transition name="translate" mode="out-in">
          <wra-header size="h1" :key="text.header">
            {{ text.header }}
          </wra-header>
        </transition>
      </v-col>
      <v-col lg="2" cols="0"></v-col>
    </v-row>

    <!--Card-->
    <v-row no-gutters>
      <v-col lg="2" cols="0"></v-col>
      <v-col lg="8" cols="12" class="minimum-height">
        <transition name="fade" mode="out-in">
          <component
            id="questionCard"
            :is="componentName"
            v-if="loaded && textLoaded"
            :text="text"
            :inputs="inputs"
            v-on:previous="previousStage"
            v-on:next="nextStage"
            v-on:scrollUp="scrollUp"
          ></component>
        </transition>
      </v-col>
      <v-col lg="2" cols="0"></v-col>
    </v-row>
  </v-container>
</template>

<script>
import Q_1 from "../components/accidentally-paid/AboutPaymentDate.vue";
import Q_2 from "../components/accidentally-paid/AboutPaymentReference.vue";
import Q_3 from "../components/accidentally-paid/AboutPaymentReason.vue";
import Q_4 from "../components/accidentally-paid/AboutRefundAmount.vue";
import Q_5 from "../components/accidentally-paid/AboutRefundName.vue";
import Q_6 from "../components/accidentally-paid/AboutRefundBank.vue";
import Q_7 from "../components/accidentally-paid/AboutRefundBankName.vue";
import Q_8 from "../components/accidentally-paid/AboutRefundBankAddress.vue";
import Q_9 from "../components/accidentally-paid/AboutRefundSameAccount.vue";
import Q_10 from "../components/accidentally-paid/AboutRefundNotSameAccountReason.vue";
import Q_11 from "../components/accidentally-paid/AboutUserName.vue";
import Q_12 from "../components/accidentally-paid/AboutUserEmail.vue";
import Q_13 from "../components/accidentally-paid/AboutUserPhoneNumber.vue";
import Q_14 from "../components/accidentally-paid/Declaration.vue";
import Q_15 from "../components/accidentally-paid/Success.vue";







const axios = require("axios");

export default {
  props: ["lang"],

  data: () => ({
    componentName: "",
    loaded: false,
    textLoaded: false,
    text: {
      
    },
    inputs: {
    }
  }),
  watch: {
    lang: {
      handler() {
        this.getText();
      }
    }
  },
  methods: {
    getText: function() {
      axios
        .get("./text/" + this.lang + "/accidentallypaid.json")
        .then((response) => {
          this.text = response.data.text;
          this.textLoaded = true;
          document.title = this.text.documentTitleText;
        });
    },
    setStage: function() {
      this.componentName = "Q_1";
      this.loaded = true;
    },
    previousStage: function(localInputs) {
      this.inputs = localInputs;
      // TransactionType - dealt with in child component ( this.$emit("reset") )
      var temp = parseInt(this.componentName.split("_")[1]);    
      if(localInputs.RefundSameAccount == "True" && this.componentName == "Q_11"){
        temp -= 2;
        this.componentName = "Q_" + temp;
      } else {
      temp -= 1;
      this.componentName = "Q_" + temp;
      }

    },
    nextStage: function(localInputs) {
      this.inputs = localInputs;
      var temp = parseInt(this.componentName.split("_")[1]);    
      if(localInputs.RefundSameAccount == "True" && this.componentName == "Q_9"){
        temp += 2;
        this.componentName = "Q_" + temp;
      } else {
      temp += 1;
      this.componentName = "Q_" + temp;
      }
      
    },
    scrollUp: function() {
      if (window.pageYOffset > 378) {
        this.$scrollTo("#questionCard", 500, {});
      }
    }
  },
  computed: {
    
  },
  mounted() {
    this.getText();
    this.setStage();
  },
  components: {
    Q_1,
    Q_2,
    Q_3,
    Q_4,
    Q_5,
    Q_6,
    Q_7,
    Q_8,
    Q_9,
    Q_10,
    Q_11,
    Q_12,
    Q_13,
    Q_14,
    Q_15
   
  }
};
</script>

<style scoped>
.fade-enter-active {
  transition: opacity 0.25s ease;
}

.fade-leave-active {
  transition: opacity 0s ease;
}

.fade-enter,
.fade-leave-to {
  opacity: 0;
}

.fade-enter-to,
.fade-leave {
  opacity: 1;
}
</style>
