import Vue from "vue";
import VueRouter from "vue-router";
import RateChecker from "../views/RateChecker.vue";
import Cookies from "../views/Cookies.vue";
import Start from "../views/Start.vue";
import CookieCrumbs from "../views/breadcrumbs/CookieCrumbs.vue";
import LttCalculator from "../views/LttCalculator.vue";
import AccidentallyPaid from "../views/AccidentallyPaid.vue";
import FeedbackBanner from "../components/ltt-calculator/FeedbackBanner.vue";
import LttPrimaryLink from "../components/ltt-calculator/LttPrimaryLink.vue";
import RateCheckerPrimaryLink from "../components/rate-checker/RateCheckerPrimaryLink.vue";
import DefaultPrimaryLink from "../components/DefaultPrimaryLink.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    components: {
      default: Start,
      primaryLink: DefaultPrimaryLink
    }
  },
  {
    path: "/rate-checker",
    name: "ratechecker",
    components: {
      default: RateChecker,
      primaryLink: RateCheckerPrimaryLink
    }
  },
  {
    path: "/accidentally-paid",
    name: "accidentallypaid",
    components: {
      default: AccidentallyPaid,
    }
  },
  {
    path: "/cookies",
    name: "cookies",
    components: {
      default: Cookies,
      breadcrumbs: CookieCrumbs,
      primaryLink: DefaultPrimaryLink
    }
  },
  {
    path: "/land-transaction-tax-calculator",
    name: "lttcalculator",
    components: {
      default: LttCalculator,
      feedback: FeedbackBanner,
      primaryLink: LttPrimaryLink
    }
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
  scrollBehavior(to, from, savedPosition) {
    return {
      x: 0,
      y: 0
    };
  }
});

export default router;
